@mixin box($size, $height: $size, $border: none,$radius: 0) {
    width: $size;
    
    @if $height != $size {
        height: $height;
    }
    @if $border != none {
        border: $border;
    }
    @if $radius != 0 {
        border-radius: $radius
    }
}

@mixin margin($size, $right: 0, $bottom: 0, $left: 0) {
    margin-top: $size;

    @if $right != 0 {
        margin-right: $right;        
    }
    @if $bottom != 0 {
        margin-bottom: $bottom;
    }
    @if $left != $right {
        margin-left: $left;
    }
}

@mixin padding($size, $right: 0, $bottom: 0, $left: 0) {
    padding-top: $size;

    @if $right != 0 {
        padding-right: $right;
    }
    @if $bottom != 0 {
        padding-bottom: $bottom;
    }
    @if $left != 0 {
        padding-left: $left;
    }
}